import React, { useState } from 'react';
import styles from './styles/ThirdStep.module.css';


interface ThirdStepProps {
    handleThirdStep: (author: string, title: string) => void;
  }

const ThirdStep: React.FC<ThirdStepProps> = ({handleThirdStep}) => {
  const [author, setAuthor] = useState('');
  const [title, setTitle] = useState('');
  const [error, setError] = useState<null | string>(null);

  const handleSubmit = () => {
    if (author.trim() && title.trim()) {
      handleThirdStep(author, title)
    } else {
      setError('  ')
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.form}>
        <h2 className={styles.title}> </h2>

        <label className={styles.label} htmlFor="author">:</label>
        <input
          type="text"
          id="author"
          value={author}
          onChange={(e) => setAuthor(e.target.value)}
          className={styles.input}
          placeholder="  "
        />

        <label className={styles.label} htmlFor="title">:</label>
        <input
          type="text"
          id="title"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className={styles.input}
          placeholder="  "
        />
         {error && <p className={styles.error}>{error}</p>}
        <button className={styles.button} onClick={handleSubmit}>
           
        </button>
      </div>
    </div>
  );
};

export default ThirdStep;
